header.schedule {
  background-color: tan;
}

div.box {
  height: calc(100vh - 56px - 10vh);
  text-align: center;
}

div.box > div {
  margin: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  right: auto;
  bottom: auto;
  width: 80vw;
}

div.box h4 {
  margin-bottom: 6vh;
}

div.box span {
  color: #28a745;
  width: auto;
  font-weight: bold;
}

div.registeredTime {
  border-bottom: 1px dashed tan;
  display: flex;
}

div.registeredTime > span:last-child {
  text-align: right;
}

div.earnings {
  display: flex;
  flex-direction: column;
  padding-top:2vw;
}
div.earnings > div {
  flex-grow: 1;
}
div.earnings > div span {
  width: 100%;
  display: block;
  text-align: center;
  border-bottom: 1px solid tan;
}

div.goals.open {
  max-height: calc(100vh - 56px - 10vh);
  overflow: scroll;

}

div.goals {
  overflow: hidden;
  max-height: 0;
  transition: max-height 0.3s ease-out;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  background: #282c34;
  width: 97vw;
  padding: 1vw;
  border: 1px solid tan;
  border-top: none;
}

div.goals .btn-success {
  width: 100%;
}

form {
  display: flex;
  flex-flow: row wrap;
  flex: 2 0px;
  align-content: stretch;
}

form input[type="number"] {
  position: relative;
  margin: auto;
  margin-right: 0;
}

form input[type="date"] {
  border: none;
  width: auto;
  text-align: left;
}

form button {
  flex-basis: 25%;
  margin: auto;
  margin-top: 1vw;
}

